
import Msg from 'vux/src/components/msg/index.vue'

export default {
data() {
return {
description: '<p class="mt20" style="font-size:16px;">送货单号：<span style="color:#aa0000">SL0000001</span></p><p class="mt20">是否查看生成的送货单</p>',
icon: 'success',
buttons: [{
type: 'primary',
text: '是',
onClick: this.confirm.bind(this)
}, {
type: 'default',
text: '否',
link: '/receiving'
}]
};
},
components: {
Msg
},
mounted() {
},
methods: {
confirm() {
this.$router.push({path: '/deliveryInfo'})
}
}
};



